header{
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    right: 0;
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px;
    background: transparent;
    transition: all ease .2s;
}

.black{
    background-color: #141414;
}

.headerLogo{
    height: 25px;
}

.headerLogo img{
    height: 100%;

}

.headerUser{
    height: 35px;
}

.headerUser img {
    height: 100%;
    border-radius: 3px;
}