@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,500;1,600;1,700;1,800&family=Roboto:wght@400;700&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
}

body {
  background-color: #111;
  color: #fff;
  margin: 0;
  font-family: 'Roboto', sans-serif;
}

.lists{
  margin-top: -100px;
}

footer{
  margin: 50px 0px;
  text-align: center;
}

.loading {
  width: 100%;
  position: fixed;
  left: 0;
  top: 0 ;
  right: 0;
  bottom: 0;
  background: rgb(0, 0, 0);
  z-index: 99;
  display: flex;
  justify-content: center;
  align-items: center;
}

.loading img{
  width: 500px;
}
